import { SelectControl, PanelBody, RangeControl, ToggleControl, Button } from "@wordpress/components";
const { MediaUpload, MediaUploadCheck } = wp.blockEditor;
import { useEffect, useState } from "react";
import { __ } from "@wordpress/i18n";

// import BMediaUpload from "../../../../../components/BMediaUpload/BMediaUpload";
import { BControlPro } from "../../../../../../../../wp-utils/v1/components/";
import { defaultEnvironmentImages, toneMappings } from '../../../../data';

// import posx from "../../../../../../public/images/envmaps/fishermans_bastion/posx.jpg";
// import posy from "../../../../../../public/images/envmaps/fishermans_bastion/posy.jpg";
