const Lighting = ({ attributes, setAttributes }) => {
  const { models, environmentImage, toneMapping, isPremium, exposure, useEnvironmentAsSkybox } = attributes;
  const [environmentImages, setEnvironmentImages] = useState(defaultEnvironmentImages);


  const updateModels = (key, value, index) => {
    const newModels = [...models];
    newModels[index][key] = value;
    setAttributes({ models: newModels });
  };

  useEffect(() => {
    if (environmentImages.filter(item => item.value === environmentImage).length === 0) {
      setEnvironmentImages((images) => [...images, { label: environmentImage, value: environmentImage }]);
    }
  }, [environmentImage])

  return (
    <PanelBody title={__("Lighting", "model-viewer")} initialOpen={false}>

      <SelectControl label={__("Environment Image", "model-viewer")} options={environmentImages} value={environmentImage} onChange={(value) => setAttributes({ environmentImage: value })} />

      <MediaUploadCheck fallback={<Button icon="upload" className="button button-primary" />}>
        <MediaUpload allowedTypes={['image']} onSelect={(file) => setAttributes({ environmentImage: file.url })} render={({ open }) => <Button className="button button-primary bp3d_upload_environment_btn" onClick={open} icon={"upload"}>{__("Upload Environment Image", "model-viewer")}</Button>} />
      </MediaUploadCheck>
      <br /> <br />

      <BControlPro isPremium={isPremium} Component={ToggleControl} label={__("Use Environment as Skybox", "model-viewer")} checked={useEnvironmentAsSkybox} onChange={() => setAttributes({ useEnvironmentAsSkybox: !useEnvironmentAsSkybox })} />

      <BControlPro
        isPremium={isPremium}
        Component={RangeControl}
        label={__("Exposure", "model-viewer")}
        value={exposure}
        onChange={(exposure) => setAttributes({ exposure })}
        min={0.1}
        step={0.1}
        max={10}
      />
      <SelectControl label={__("Tone Mapping", "model-viewer")} options={toneMappings} value={toneMapping} onChange={(value) => setAttributes({ toneMapping: value })} />


      {/* <PanelRow> */}

      {/* <div className="environmentImages">
          <img src={posx} className={environmentImage === posx ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
          <img src={posy} className={environmentImage === posy ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
          <img src={posz} className={environmentImage === posz ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
          <img src={negx} className={environmentImage === negx ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
          <img src={negy} className={environmentImage === negy ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
          <img src={negz} className={environmentImage === negz ? "image-active" : ""} onClick={(e) => setAttributes({ environmentImage: e.target.src })} />
        </div> */}
      {/* </PanelRow> */}
      {/* <PanelRow></PanelRow> */}
      {/* <BMediaUpload
        value={environmentImage}
        placeholder={__("Custom", "model-viewer")}
        onSelect={(environmentImage) => setAttributes({ environmentImage })}
        // type={["model/gltf-binary"]}
        label={__("Custom", "model-viewer")}
      /> */}
    </PanelBody>
  );
};

export default Lighting;
