import { __ } from "@wordpress/i18n";
import { useEffect, useState } from "@wordpress/element";
import { compose } from "@wordpress/compose";
import { withSelect } from "@wordpress/data";

import { PanelBody, PanelRow, Button } from "@wordpress/components";
import BColor from "../../../../../../components/BColor/BColor";
import BMediaUpload from "../../../../../../components/BMediaUpload/BMediaUpload";
import BUnitControl from "../../../../../../components/BUnitControl/BUnitControl";
import BDevice from "../../../../../../../../bpl-tools/Components/Deprecated/BDevice/BDevice";

// import BDevice from '../../../../../../../'
import { BControlPro } from "../../../../../../../../wp-utils/v1/components/";
import { Device } from "../../../../../../../../bpl-tools/Components";

const Style = ({ attributes, setAttributes, device }) => {
  const { styles, woo, currentViewer } = attributes;
  const { width, height, bgColor, bgImage, progressBar, progressBarColor, thumbSize = "90px" } = styles;


  useEffect(() => {
    if (typeof height === "string") {
      setAttributes({
        styles: {
          ...styles,
          height: {
            desktop: height,
            tablet: height,
            mobile: height,
          },
        },
      });
    }
    if (typeof width === "string") {
      setAttributes({
        styles: {
          ...styles,
          width: {
            desktop: width,
            tablet: width,
            mobile: width,
          },
        },
      });
    }
  }, []);

  return (
    <PanelBody title={__("Style", "model-viewer")}>
      {!woo && (
        <>
          <BUnitControl
            labelPosition="left"
            label={<PanelRow className="gap5">{__("Width ", "model-viewer")} {" "}<Device /></PanelRow>}
            value={width[device] || width}
            onChange={(value) => setAttributes({ styles: { ...styles, width: { ...styles.width, [device]: value } } })}
            units={[
              { value: "px", label: "px", default: 500 },
              { value: "%", label: "%", default: 100 },
            ]}
          />

        </>
      )}
      <BUnitControl
        label={<PanelRow className="gap5">{__("Height ", "model-viewer")} {" "}<Device /></PanelRow>}
        value={height[device] || height}
        labelPosition="side"
        onChange={(value) => setAttributes({ styles: { ...styles, height: { ...styles.height, [device]: value } } })}
        units={[
          { value: "px", label: "px", default: 500 },
          { value: "%", label: "%", default: 100 },
          { value: "vh", label: "vh", default: 100 },
        ]}
      />
      <BUnitControl label={__("Thumb Size", "model-viewer")} labelPosition="side" value={thumbSize} onChange={(thumbSize) => setAttributes({ styles: { ...styles, thumbSize } })} units={[{ value: "px", label: "px", default: 100 }]} />
      <PanelRow>
        <label>{__("Background Color", "model-viewer")}</label>
        <BColor value={bgColor || "#ffffff"} defaultColor="#ffffff" onChange={(bgColor) => setAttributes({ styles: { ...styles, bgColor } })} />
      </PanelRow>
      {currentViewer === "modelViewer" && (
        <>
          <PanelRow>
            <label>
              <strong>{__("Note: Color will be ignore if background image used", "model-viewer")}</strong>
            </label>
          </PanelRow>
          <br />
          <BControlPro
            Component={BMediaUpload}
            media={true}
            label={__("Background Image", "model-viewer")}
            placeholder={__("Background Image", "model-viewer")}
            value={bgImage}
            onSelect={(bgImage) => {
              setAttributes({ styles: { ...styles, bgImage } });
            }}
            isPremium={true}
          />
          {progressBar && (
            <PanelRow>
              <label>{__("ProgressBar Color", "model-viewer")}</label>
              <BColor value={progressBarColor || "#666"} defaultColor="#666" onChange={(progressBarColor) => setAttributes({ styles: { ...styles, progressBarColor } })} />
            </PanelRow>
          )}
        </>
      )}
    </PanelBody>
  );
};

export default compose(
  withSelect((select) => {
    const { getDeviceType } = select('core/editor');

    return {
      device: getDeviceType()?.toLowerCase(),
    };
  }),
)(Style);
