import { __ } from "@wordpress/i18n";
import { useEffect, useState, useRef } from "@wordpress/element";
const { withSelect } = wp.data;
import { useBlockProps } from "@wordpress/block-editor";

import Settings from "./settings";
import Viewer from "../Common/Viewer";
import { useWPAjaxQuery } from "../../../../../../wp-utils/v1";

const Edit = (props) => {
  const { clientId, attributes, setAttributes, isSelected, postType } = props;
  const [isValid, setIsValid] = useState(true);
  const { uniqueId, model, models, multiple, additional, align, woo, isPremium = true } = attributes;
  const { data, isLoading } = useWPAjaxQuery(bp3dBlock?.ajaxURL, { action: "bp3d_pipe_checker", _wpnonce: bp3dBlock?.nonce });
  const [modelReader, setModelReader] = useState(null);

  const viewerRef = useRef();

  //generate new unique ID
  useEffect(() => {
    const woo = ["product", null].includes(postType) ? true : false;
    setAttributes({ uniqueId: "b3dviewer" + clientId.substr(0, 8), woo });
  }, []);

  useEffect(() => {
    setAttributes({ isPremium: data?.isPipe });
    if (!data?.isPipe && !isLoading) {
      setAttributes({ multiple: false });
    }
  }, [data, isLoading]);

  useEffect(() => {
    try {
      new URL(modelSrc);
      setIsValid(true);
    } catch (error) {
      setIsValid(false);
    }
  }, [uniqueId, model, models, multiple]);

  const modelSrc = multiple ? models[0]?.modelUrl : model?.modelUrl;

  const blockProps = useBlockProps();


  if (isLoading) {
    return <h2>Loading...</h2>;
  }


  return (
    <div {...blockProps}>
      {/* <div id={`${uniqueId}`} className={`b3dviewer align${align} ${woo ? "woocommerce" : ""}`}> */}
      {!isSelected && <div className="modelViewerIsSelected"></div>}
      <>
        <Settings {...{ modelReader, setModelReader, attributes, setAttributes, viewerRef }} />

        {modelSrc && isValid && <Viewer  {...{ modelReader, setModelReader, viewerRef, attributes, setAttributes, __ }} />}
        {modelSrc && !isValid && <h2>{__("3D file is not valid", "model-viewer")}</h2>}
        {!modelSrc && (
          <div className="upload3d">
            <h2>{__("Please upload a model to preview.", "model-viewer")}</h2>
          </div>
        )}
      </>
      {/* </div> */}
    </div>
  );
};

export default withSelect((select) => {
  const { isEditorSidebarOpened } = select("core/edit-post");
  const postType = select("core/editor")?.getCurrentPostType() || "product";
  return {
    isSidebarOpened: isEditorSidebarOpened(),
    postType,
  };
})(Edit);
